.scheda-lavorazione-container {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 15px;
}

.layout-container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
}

.informazioni-generali-section {
    background: linear-gradient(145deg, var(--primary-bg), #f8f9fa);
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

.informazioni-generali-section h3 {
    color: var(--accent-color);
    font-size: 1.5rem;
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 2px solid rgba(0,0,0,0.06);
}

.form-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
}

.form-group {
    background: rgba(255, 255, 255, 0.9);
    padding: 8px 12px;
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 8px;
    border: 1px solid rgba(0,0,0,0.06);
}

.form-group label {
    color: var(--text-secondary);
    font-size: 0.85rem;
    margin-bottom: 4px;
    font-weight: 500;
}

.form-group p {
    color: var(--text-primary);
    font-size: 0.95rem;
    margin: 0;
    line-height: 1.3;
}

.note-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    margin-top: 15px;
}

.note-field {
    background: var(--primary-bg);
    padding: 15px;
    border-radius: 8px;
    border: 1px solid rgba(0,0,0,0.06);
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.note-field h4 {
    color: var(--accent-color);
    font-size: 0.85rem;
    margin-bottom: 4px;
}

.note-content {
    font-size: 0.95rem;
    line-height: 1.3;
    color: var(--text-primary);
}

.prodotti-section {
    grid-column: 1 / -1;
    margin-top: 20px;
    border-top: 1px solid var(--border-color);
    padding-top: 20px;
}

.prodotti-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 15px;
    width: 100%;
}

.prodotto-card {
    background: var(--primary-bg);
    border: 1px solid var(--border-color);
    border-radius: 8px;
    padding: 15px;
}

.prodotto-header {
    margin-bottom: 10px;
    padding-bottom: 5px;
    border-bottom: 1px solid var(--border-color);
}

.prodotto-header h5 {
    margin: 0;
    color: var(--accent-color);
    font-size: 1rem;
}

.detail-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.detail-row label {
    color: var(--text-secondary);
    font-size: 0.85rem;
}

.detail-row span {
    color: var(--text-primary);
    font-size: 0.9rem;
}

.button-container {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 20px;
}

.edit-button, .select-button {
    background: var(--accent-color);
    color: white;
    border: none;
    border-radius: 6px;
    padding: 8px 16px;
    font-size: 0.9rem;
    cursor: pointer;
    transition: background-color 0.2s;
}

.edit-button:hover, .select-button:hover {
    background: var(--accent-hover);
}

.message {
    padding: 10px;
    border-radius: 6px;
    margin: 10px 0;
    text-align: center;
    font-size: 0.9rem;
}

.message.success {
    background: var(--success-color);
    color: white;
}

.message.error {
    background: var(--error-color);
    color: white;
}

@media (max-width: 1200px) {
    .form-container,
    .note-section {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .form-container,
    .note-section {
        grid-template-columns: 1fr;
    }
    
    .form-group,
    .note-field {
        height: auto;
        min-height: 60px;
    }
}
