/* Layout principale */
.lavorazioni-dashboard {
    padding: var(--spacing-lg);
    background-color: var(--secondary-bg);
    min-height: 100vh;
}

/* Header e titolo */
.lavorazioni-dashboard h2 {
    color: var(--text-primary);
    margin-bottom: var(--spacing-md);
    font-size: 24px;
    font-weight: 600;
}

/* Container statistiche */
.stats-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin: 20px 0;
    padding: 20px;
    background: var(--primary-bg);
    border-radius: var(--border-radius);
    box-shadow: var(--card-shadow);
}

.stat-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background: linear-gradient(145deg, #ffffff, #f8f9fa);
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stat-box:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

.stat-label {
    font-size: 14px;
    color: var(--text-secondary);
    margin-bottom: 8px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.stat-value {
    font-size: 24px;
    font-weight: 600;
    color: var(--text-primary);
}

.stat-box.urgent {
    background: linear-gradient(145deg, #ff6b6b, #f03e3e);
}

.stat-box.urgent .stat-label,
.stat-box.urgent .stat-value {
    color: white;
}

/* Tabella lavorazioni */
.table-container {
    margin-top: var(--spacing-lg);
    background: var(--primary-bg);
    border-radius: var(--border-radius);
    box-shadow: var(--card-shadow);
    overflow: hidden;
}

.table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
}

.table th {
    background: var(--secondary-bg);
    padding: 15px;
    text-align: left;
    font-weight: 600;
    color: var(--text-primary);
    border-bottom: 2px solid var(--border-color);
}

.table td {
    padding: 12px 15px;
    border-bottom: 1px solid var(--border-color);
    color: var(--text-primary);
}

.table tr:hover {
    background-color: rgba(0, 0, 0, 0.02);
}

/* Pulsanti e azioni */
.button-container {
    margin: 20px 0;
    display: flex;
    gap: 10px;
}

.edit-button, .delete-button {
    padding: 8px 16px;
    border-radius: var(--border-radius-sm);
    border: none;
    cursor: pointer;
    font-weight: 500;
    transition: all 0.3s ease;
}

.edit-button {
    background: var(--accent-color);
    color: white;
    text-decoration: none;
}

.delete-button {
    background: var(--error-color);
    color: white;
}

.edit-button:hover {
    background: var(--accent-hover);
    transform: translateY(-1px);
}

.delete-button:hover {
    background: #c82333;
    transform: translateY(-1px);
}

/* Messaggi di stato */
.error-message {
    background-color: var(--error-bg);
    color: var(--error-color);
    padding: 12px;
    border-radius: var(--border-radius-sm);
    margin: 10px 0;
    text-align: center;
}

.no-lavorazioni-message {
    text-align: center;
    padding: 40px;
    color: var(--text-secondary);
}

/* Loading state */
.loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 200px;
}

/* Media Queries */
@media (max-width: 1200px) {
    .stats-container {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 992px) {
    .stats-container {
        grid-template-columns: repeat(2, 1fr);
    }

    .table {
        font-size: 14px;
    }
}

@media (max-width: 768px) {
    .lavorazioni-dashboard {
        padding: var(--spacing-md);
    }

    .stats-container {
        grid-template-columns: 1fr;
        padding: 15px;
    }

    .table-container {
        overflow-x: auto;
    }

    .edit-button, .delete-button {
        padding: 6px 12px;
        font-size: 12px;
    }
}

@media (max-width: 480px) {
    .lavorazioni-dashboard h2 {
        font-size: 20px;
    }

    .stat-label {
        font-size: 12px;
    }

    .stat-value {
        font-size: 18px;
    }

    .table td, .table th {
        padding: 8px;
    }
}
