.ingredienti-container {
    width: 100%;
    height: 100%;
    background: linear-gradient(145deg, var(--primary-bg), #f8f9fa);
    border-radius: var(--border-radius-lg);
    padding: var(--spacing-xl);
    box-shadow: 0 10px 30px rgba(0,0,0,0.08);
    position: relative;
    overflow: hidden;
    background: 
        linear-gradient(145deg, #ffffff, #f8f9fa),
        radial-gradient(circle at 0 0, rgba(40, 167, 69, 0.03) 0%, transparent 50%),
        radial-gradient(circle at 100% 100%, rgba(40, 167, 69, 0.03) 0%, transparent 50%);
}

.ingredienti-container::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, transparent 50%, #28a745 50%);
    opacity: 0.1;
    border-radius: 0 var(--border-radius-lg) 0 0;
}

.ingredienti-container::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(-45deg, #28a745 50%, transparent 50%);
    opacity: 0.1;
    border-radius: 0 0 0 var(--border-radius-lg);
}

.ingredienti-content h3 {
    color: #28a745;
    font-size: var(--font-size-xl);
    margin-bottom: var(--spacing-lg);
    padding-bottom: var(--spacing-sm);
    border-bottom: 2px solid rgba(40, 167, 69, 0.2);
}

.ingredienti-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    margin-top: var(--spacing-md);
}

.ingredienti-table th {
    background: linear-gradient(to right, #28a745, #34ce57);
    color: white;
    padding: var(--spacing-md);
    font-weight: 500;
    text-align: left;
    font-size: var(--font-size-md);
}

.ingredienti-table td {
    padding: var(--spacing-md);
    background: rgba(255, 255, 255, 0.8);
    border-bottom: 1px solid rgba(40, 167, 69, 0.1);
    font-size: var(--font-size-md);
    line-height: 1.5;
}

.ingredienti-table tr:hover td {
    background: rgba(40, 167, 69, 0.05);
}

.no-ricetta, .loading, .error {
    text-align: center;
    padding: var(--spacing-xl);
    color: var(--text-secondary);
    font-size: var(--font-size-md);
}

.error {
    color: var(--error-color);
}

@media (max-width: 1200px) {
    .ingredienti-container {
        width: 100%;
    }
}

@media (max-width: 768px) {
    .dettaglio-lavorazione {
        grid-template-columns: 1fr;
    }
}
