.passaggi-container {
    width: 100%;
    margin-left: auto;
    background: linear-gradient(145deg, var(--primary-bg), #f8f9fa);
    border-radius: var(--border-radius-lg);
    padding: var(--spacing-xl);
    box-shadow: 0 10px 30px rgba(0,0,0,0.08);
    position: relative;
    overflow: hidden;
    background: 
        linear-gradient(145deg, #ffffff, #f8f9fa),
        radial-gradient(circle at 0 0, rgba(102, 16, 242, 0.03) 0%, transparent 50%),
        radial-gradient(circle at 100% 100%, rgba(102, 16, 242, 0.03) 0%, transparent 50%);
}

.passaggi-container::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, transparent 50%, #6610f2 50%);
    opacity: 0.1;
    border-radius: 0 var(--border-radius-lg) 0 0;
}

.passaggi-container::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(-45deg, #6610f2 50%, transparent 50%);
    opacity: 0.1;
    border-radius: 0 0 0 var(--border-radius-lg);
}

.passaggi-container h3 {
    color: #6610f2;
    font-size: var(--font-size-xl);
    margin-bottom: var(--spacing-lg);
    padding-bottom: var(--spacing-sm);
    border-bottom: 2px solid rgba(102, 16, 242, 0.2);
}

.passaggi-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    margin-top: var(--spacing-md);
}

.passaggi-table th {
    background: linear-gradient(to right, #6610f2, #7c3aed);
    color: white;
    padding: var(--spacing-md);
    font-weight: 500;
    text-align: left;
}

.passaggi-table td {
    padding: var(--spacing-md);
    background: rgba(255, 255, 255, 0.8);
    border-bottom: 1px solid rgba(102, 16, 242, 0.1);
}

.passaggi-table tr:hover td {
    background: rgba(102, 16, 242, 0.05);
}

.passaggi-table input[type="time"],
.passaggi-table input[type="text"] {
    width: 100%;
    padding: var(--spacing-sm);
    border: 1px solid rgba(102, 16, 242, 0.2);
    border-radius: var(--border-radius-sm);
    font-size: var(--font-size-md);
}

.passaggi-table input:focus {
    outline: none;
    border-color: #6610f2;
    box-shadow: 0 0 0 3px rgba(102, 16, 242, 0.1);
}

.ora-corrente {
    background: linear-gradient(to right, #6610f2, #7c3aed);
    color: white;
    padding: var(--spacing-xs) var(--spacing-sm);
    border: none;
    border-radius: var(--border-radius-sm);
    cursor: pointer;
    transition: all 0.3s ease;
}

.ora-corrente:hover {
    background: linear-gradient(to right, #7c3aed, #6610f2);
    transform: translateY(-1px);
}

.save-button {
    background: linear-gradient(to right, #6610f2, #7c3aed);
    color: white;
    padding: var(--spacing-sm) var(--spacing-lg);
    border: none;
    border-radius: var(--border-radius-sm);
    font-size: var(--font-size-md);
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: var(--spacing-lg) auto;
    display: block;
}

.save-button:hover {
    background: linear-gradient(to right, #7c3aed, #6610f2);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(102, 16, 242, 0.2);
}

@media (max-width: 1200px) {
    .passaggi-container {
        width: 100%;
    }
}

@media (max-width: 768px) {
    .passaggi-container {
        padding: var(--spacing-md);
    }
    
    .passaggi-table {
        font-size: var(--font-size-sm);
    }
}
