.conservazione-container {
    width: 100%;
    margin-left: auto;
    background: linear-gradient(145deg, var(--primary-bg), #f8f9fa);
    border-radius: var(--border-radius-lg);
    padding: var(--spacing-xl);
    box-shadow: 0 10px 30px rgba(0,0,0,0.08);
    position: relative;
    overflow: hidden;
    background: 
        linear-gradient(145deg, #ffffff, #f8f9fa),
        radial-gradient(circle at 0 0, rgba(40, 167, 69, 0.03) 0%, transparent 50%),
        radial-gradient(circle at 100% 100%, rgba(40, 167, 69, 0.03) 0%, transparent 50%);
}

.conservazione-container::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, transparent 50%, #28a745 50%);
    opacity: 0.1;
    border-radius: 0 var(--border-radius-lg) 0 0;
}

.conservazione-container::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background: linear-gradient(-45deg, #28a745 50%, transparent 50%);
    opacity: 0.1;
    border-radius: 0 0 0 var(--border-radius-lg);
}

.conservazione-container h3 {
    color: #28a745;
    font-size: var(--font-size-xl);
    margin-bottom: var(--spacing-lg);
    padding-bottom: var(--spacing-sm);
    border-bottom: 2px solid rgba(40, 167, 69, 0.2);
}

.section {
    margin-bottom: var(--spacing-lg);
}

.section h4 {
    color: #28a745;
    margin-bottom: var(--spacing-md);
    font-size: var(--font-size-lg);
}

.checkbox-group {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: var(--spacing-md);
}

.checkbox-group label {
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
    color: var(--text-primary);
    cursor: pointer;
}

.checkbox-group input[type="checkbox"] {
    width: 18px;
    height: 18px;
    border: 2px solid rgba(40, 167, 69, 0.2);
    border-radius: 4px;
    cursor: pointer;
}

.checkbox-group input[type="checkbox"]:checked {
    background-color: #28a745;
    border-color: #28a745;
}

.input-group {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: var(--spacing-lg);
}

.input-group label {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-sm);
    color: var(--text-secondary);
}

.input-group input {
    padding: var(--spacing-sm);
    border: 1px solid rgba(40, 167, 69, 0.2);
    border-radius: var(--border-radius-sm);
    font-size: var(--font-size-md);
}

.input-group input:focus {
    outline: none;
    border-color: #28a745;
    box-shadow: 0 0 0 3px rgba(40, 167, 69, 0.1);
}

.save-button {
    background: linear-gradient(to right, #28a745, #34ce57);
    color: white;
    padding: var(--spacing-sm) var(--spacing-lg);
    border: none;
    border-radius: var(--border-radius-sm);
    font-size: var(--font-size-md);
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    margin: var(--spacing-lg) auto;
    display: block;
}

.save-button:hover {
    background: linear-gradient(to right, #34ce57, #28a745);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(40, 167, 69, 0.2);
}

@media (max-width: 768px) {
    .conservazione-container {
        padding: var(--spacing-md);
    }
    
    .checkbox-group {
        grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    }
    
    .input-group {
        grid-template-columns: 1fr;
    }
}
